<template>
    <div>
        <v-navigation-drawer v-model="chapter" fixed app temporary>            
            <v-toolbar flat>
            <v-list color="deep-purple lighten-1">
                <v-list-tile>
                <v-list-tile-title class="title">
                    Navigate Here
                </v-list-tile-title>
                </v-list-tile>
            </v-list>
            </v-toolbar>

            <v-divider></v-divider>

            <v-list dense class="pt-0">
            <v-list-tile
                v-for="item in items"
                :key="item.title"
                @click="onward = !onward"
            >
                <v-list-tile-action>
                <v-icon>{{ item.icon }}</v-icon>
                </v-list-tile-action>

                <v-list-tile-content>
                <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                </v-list-tile-content>
            </v-list-tile>
            </v-list>
        </v-navigation-drawer>
        <div id="dyn-container">        
            <a href="#" @click="chapter = !chapter">Test</a>
            <h1 class="header" :class="{headerFade: isActive, header: !isActive}">{{content.data.novel}}</h1>        
            <div class="body" v-html="content.data.content"></div>
        </div>
    </div>    
</template>

<script>
const axios = require('axios')

export default {
  data () {
    return {
      content: null,
      isActive: false,
      chapter: false,
      items: [
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'PROLOGUE', icon: 'dashboard' },
        { title: 'About', icon: 'question_answer' }
      ],
      right: null
    }
  },
  mounted () {
    axios
      .get('static/chapters/chapter.json')
      .then(response => (this.content = response))
      .then(this.isActive = true)
      .then(this.$Progress.finish())
  },
  created () {
    //  [App.vue specific] When App.vue is first loaded start the progress bar
    this.$Progress.start()
    //  hook the progress bar to start before we move router-view
  }
}
</script>

<style lang="scss" scoped>
    #dyn-container{
        max-width: 700px;
        margin: 0 auto;
        padding: 20px;
        font-size: 1.2rem;
    }
    .header{        
        margin-top: 140px;
        margin-bottom: 34px;      
        opacity: 0;
        transform: translateY(-20px);
        transition: all 3s ease-in;  
    }
    .headerFade{
        margin-top: 140px;
        margin-bottom: 34px;   
        opacity: 1;
        transform: translateY(0);
    }
    .chapter{
        color: #9575CD;
    }
    .body{
        font-family: 'EB Garamond', serif;
        opacity: 0.8;
    }
    .fade-enter-active, .fade-leave-active {
      transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
     opacity: 0;
    }
</style>



